

let initState = {
    islogin: false
}

const login = (state = initState, action) => {
    switch (action.type) {
        case 'LONGIN_TRUE':
            return {
                ...state,
                islogin: true
            };
        case 'LOGIN_F':
            return {
                ...state,
                islogin: false
            };
        default:
            return {...state};
    }
}

export default login;
